.header {
  height: 115px;
  padding: 0 40px;
}
.header img {
  margin-top: 26px;
  float: left;
}
.header button {
  width: 165px;
  height: 55px;
  background-color: #e8420a;
  border: none;
  border-radius: 7px;
  color: #fff;
  font-size: 28px;
  float: left;
  margin: 36px 0 0 180px;
}
.header i {
  font-size: 60px;
  float: right;
  margin-top: 35px;
}
